// dodanie podanego filmu do listy obejrzanych filmw
export const addToWatchedMovies = movieDetails => {
  // pobiera list obejrzanych filmw z localStorage lub tworzy now, jeli nie istnieje
  let watchedMovies = JSON.parse(localStorage.getItem('watchedMovies')) || [];

  // prawdopodobnie niepotrzebny fragment kodu ============== do usunicia
  // if (!Array.isArray(watchedMovies)) {
  //   watchedMovies = [];
  // }

  // sprawdza, czy film jest ju na licie obejrzanych
  let isMovieAlreadyWatched = watchedMovies.find(movie => movie.id === movieDetails.id);
  //  pobiera list filmw do ogldnicia z localStorage lub tworzy now, jeli nie istnieje
  let queuedMovies = JSON.parse(localStorage.getItem('queuedMovies')) || [];
  // jeli filmu nie ma na licie obejrzanych
  if (!isMovieAlreadyWatched) {
    // dodaje film do listy obejrzanych i przesya zaktualizowan list do localStorage
    watchedMovies.push(movieDetails);
    localStorage.setItem('watchedMovies', JSON.stringify(watchedMovies));
    // usuwa film z listy filmw do obejrzenia, jeli na niej jest i przesya zaktualizowan list do localStorage
    queuedMovies = queuedMovies.filter(movie => movie.id !== movieDetails.id);
    localStorage.setItem('queuedMovies', JSON.stringify(queuedMovies));
    // refreshView(); 
  } else {
    //console.log(`"${movieDetails.title}" is already in Watched Movies`);
  }
};

// dodanie podanego filmu do listy filmw do ogldnicia
export const addToQueue = movieDetails => {
  //  pobiera list filmw do ogldnicia z localStorage lub tworzy now, jeli nie istnieje
  let queuedMovies = JSON.parse(localStorage.getItem('queuedMovies')) || [];
  // sprawdza, czy film jest ju na licie film do obejrzenia
  let isMovieInQueue = queuedMovies.find(movie => movie.id === movieDetails.id);

  // pobiera list obejrzanych filmw z localStorage lub tworzy now, jeli nie istnieje
  let watchedMovies = JSON.parse(localStorage.getItem('watchedMovies')) || [];
  // jeli filmu nie ma na licie filmw do obejrzenia
  if (!isMovieInQueue) {
    // dodaje film do listy obejrzanych i przesya zaktualizowan list do localStorage
    queuedMovies.push(movieDetails);
    localStorage.setItem('queuedMovies', JSON.stringify(queuedMovies));
    // usuwa film z listy filmw do obejrzenia, jeli na niej jest i przesya zaktualizowan list do localStorage
    watchedMovies = watchedMovies.filter(movie => movie.id !== movieDetails.id);
    localStorage.setItem('watchedMovies', JSON.stringify(watchedMovies));
    // refreshView();
  } else {
    //console.log(`"${movieDetails.title}" is already in Queue`);
  }
};

