// pobranie wskanika okna modalnego
const modal = document.getElementById("modalGoit");
// pobranie wskanika przycisku, ktry otwiera okno modalne
const btn = document.querySelector(".footer-btn");
// pobranie wskanika elementu <span>, ktry zamyka okno modalne
const span = document.querySelector(".close-modal-btn-goit");

// obsuga kliknicia na przycisk otwierajcy okno modalne 'goit-team'
btn.onclick = function() {
  modal.style.display = "block";
}

// obsuga kliknicia na przycisk zamykajcy okno modalne
span.onclick = function() {
  modal.style.display = "none";
}

// obsuga kliknicia poza okno modalne zamykajcego to okno
window.onclick = function(event) {
  if (event.target == modal) {
    modal.style.display = "none";
  }
}

// obsuga zdarzenia nacinicia klawisza Esc zamkykajcego okno modalne
document.onkeydown = function(event) {
  if (event.key === "Escape") {
    modal.style.display = "none";
  }
}
