// przeczanie 
export const toggleModal = (modalId) => {
  const modal = document.getElementById(modalId);

  if (modal) {
      modal.style.display = modal.style.display === 'block' ? 'none' : 'block';
  }
};
      //wydaje mi si e chciae zrobi to w ten sposb kwestia wyboru
//    if (modal) {
//     modal.style.visibility = modal.style.visibility === 'visible' ? 'hidden' : 'visible';
//   }
// };

const openModalBtns = document.querySelectorAll('.openModalBtn');

// dodanie obsugi kliknicia dla kadej karty filmu, ktre otwiera okno modalne ze szczegami filmu
openModalBtns.forEach((btn) => {
  btn.addEventListener('click', () => {
    const modalId = btn.getAttribute('id');
    toggleModal(modalId);
  });
});